import { useRouter } from 'next/router'
import { useEffect, useState } from 'react'
import Navbar from '../../components/Navbar'
import Card from '../../components/Card'
import Tooltip from '../../components/Tooltip'
import { saveSessionFeedback } from '../../lib/feedback'
import { getExerciseWeight, setExerciseWeight } from '../../lib/weights'
import { getAlternatives } from '../../lib/planGenerator'

export default function DayPage() {
  const router = useRouter()
  const { day } = router.query

  const [session, setSession] = useState(null)
  const [loading, setLoading] = useState(true)
  const [swapIndex, setSwapIndex] = useState(null)
  const [alts, setAlts] = useState([])

  useEffect(() => {
    if (!day) return

    fetch('/api/plan')
      .then(r => r.json())
      .then(plan => {
        const index = Number(day) - 1
        if (plan.sessions && plan.sessions[index]) {
          const localSession = JSON.parse(JSON.stringify(plan.sessions[index]))
          setSession(localSession)
        }
        setLoading(false)
      })
  }, [day])

  function handleSubmit(e) {
    e.preventDefault()
    const form = e.target
    const difficulty = form.difficulty.value
    const soreness = form.soreness.value
    const missed = form.missed.checked
    const notes = form.notes.value

    saveSessionFeedback({
      sessionIndex: Number(day) - 1,
      difficulty,
      soreness,
      missedReps: missed,
      notes,
    })

    alert('Feedback saved! Your plan will adapt when you regenerate it.')
    form.reset()
  }

  function handleWeightSave(exName, inputEl, unit = 'kg') {
    const val = inputEl.value
    if (!val) return
    setExerciseWeight(exName, Number(val), unit)
    alert(`Saved ${val}${unit} for ${exName}`)
  }

  function getWeightDisplay(exName) {
    const w = getExerciseWeight(exName)
    if (!w) return null
    return `${w.weight}${w.unit}`
  }

  function openSwap(i, exercise) {
    const eq = (exercise.equipment && exercise.equipment[0]) || 'barbell'
    let poolKey = 'gym'
    if (eq === 'dumbbell' || eq === 'bands') poolKey = 'dumbbell'
    if (eq === 'bodyweight') poolKey = 'body'
    const suggestions = getAlternatives(exercise.name, poolKey, 5)
    setAlts(suggestions)
    setSwapIndex(i)
  }

  function doSwap(i, newEx) {
    if (!session) return
    const updated = { ...session }
    updated.exercises = updated.exercises.map((ex, idx) => (idx === i ? { ...newEx, sets: ex.sets, reps: ex.reps, intensity_pct: ex.intensity_pct, rest_sec: ex.rest_sec, rir: ex.rir, notes: ex.notes } : ex))
    setSession(updated)
    setSwapIndex(null)
    setAlts([])
  }

  if (loading) {
    return (
      <>
        <Navbar />
        <main className="container p-8">
          <p className="text-slate-600 dark:text-slate-300">Loading session...</p>
        </main>
      </>
    )
  }

  if (!session) {
    return (
      <>
        <Navbar />
        <main className="container p-8">
          <p className="text-red-500">Invalid session/day.</p>
        </main>
      </>
    )
  }

  return (
    <>
      <Navbar />

      <main className="container p-8">
        <h2 className="text-3xl font-bold mb-6">{session.day}</h2>

        <div className="grid gap-4 mb-10">
          {session.exercises.map((ex, i) => (
            <Card key={i}>
              <div className="flex items-start justify-between gap-4">
                <div className="flex-1">
                  <h3 className="font-semibold text-lg">{ex.name}</h3>
                  <p className="text-sm text-slate-600 dark:text-slate-300">{ex.notes}</p>

                  <div className="mt-2 text-sm text-slate-300 flex flex-wrap gap-2 items-center">
                    <span className="font-semibold">{ex.sets} √ó {ex.reps}</span>
                    <Tooltip label="Sets √ó Reps: Do this many sets, each with this many repetitions." />

                    <span>{ex.intensity_pct}%</span>
                    <Tooltip label="Training intensity: Percentage of your estimated one-rep max (1RM)." />

                    <span>{ex.rest_sec}s rest</span>
                    <Tooltip label="Rest time: How long to rest between sets for optimal recovery." />

                    {ex.rir !== undefined && (
                      <>
                        <span>RIR {ex.rir}</span>
                        <Tooltip label="Reps in Reserve (RIR): Leave this many reps before failure to manage fatigue safely." />
                      </>
                    )}
                  </div>

                  <div className="mt-3 flex items-center gap-2">
                    <input defaultValue={getWeightDisplay(ex.name) ? getWeightDisplay(ex.name).replace('kg','') : ''} placeholder="Enter kg" id={`weight-${i}`} className="p-2 border rounded w-36" />
                    <button onClick={() => handleWeightSave(ex.name, document.getElementById(`weight-${i}`), 'kg')} className="small-btn">Save weight</button>
                    <div className="text-xs text-slate-500 ml-2">{getWeightDisplay(ex.name) || 'No saved weight'}</div>
                  </div>
                </div>

                <div className="flex flex-col items-end gap-2">
                  <button onClick={() => openSwap(i, ex)} className="small-btn">Swap</button>
                </div>
              </div>

              {swapIndex === i && alts && alts.length > 0 && (
                <div className="mt-3 border-t pt-3">
                  <div className="text-sm text-slate-700 dark:text-slate-300 mb-2">Swap with:</div>
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-2">
                    {alts.map((a, ai) => (
                      <button key={ai} onClick={() => doSwap(i, a)} className="p-2 rounded border text-left small-btn">
                        <div className="font-semibold">{a.name}</div>
                        <div className="text-xs text-slate-500">{(a.equipment || []).join(', ')}</div>
                      </button>
                    ))}
                  </div>
                  <div className="mt-2">
                    <button onClick={() => { setSwapIndex(null); setAlts([]) }} className="text-xs small-btn">Cancel</button>
                  </div>
                </div>
              )}
            </Card>
          ))}
        </div>

        <Card title="Log Your Feedback">
          <form onSubmit={handleSubmit} className="grid gap-4">
            <div>
              <label className="font-semibold">How difficult was this session?</label>
              <select name="difficulty" className="p-2 border rounded w-full mt-1">
                <option>Easy</option>
                <option>Moderate</option>
                <option>Hard</option>
              </select>
            </div>

            <div>
              <label className="font-semibold">How sore do you feel?</label>
              <select name="soreness" className="p-2 border rounded w-full mt-1">
                <option>None</option>
                <option>Mild</option>
                <option>High</option>
              </select>
            </div>

            <div className="flex items-center gap-2">
              <input type="checkbox" name="missed" id="missed" />
              <label htmlFor="missed">Missed any reps?</label>
            </div>

            <div>
              <label className="font-semibold">Notes</label>
              <textarea name="notes" className="p-2 border rounded w-full mt-1" placeholder="Any discomfort, fatigue, injuries, easy/hard parts..." />
            </div>

            <button className="btn-primary w-full">Save Feedback</button>
          </form>
        </Card>
      </main>
    </>
  )
}
